import {
    CHECKOUT_CREATE_FAILURE,
    CHECKOUT_CREATE_REQUEST,
    CHECKOUT_CREATE_SUCCESS,
    CHECKOUT_FETCH_FAILURE,
    CHECKOUT_FETCH_REQUEST,
    CHECKOUT_FETCH_SUCCESS,
    CHECKOUT_UPDATE_FAILURE,
    CHECKOUT_UPDATE_REQUEST,
    CHECKOUT_UPDATE_SUCCESS,
    CreateCheckoutFailurePayload,
    CreateCheckoutPayload,
    CreateCheckoutSuccessPayload,
    FetchCheckoutFailurePayload,
    FetchCheckoutPayload,
    FetchCheckoutSuccessPayload,
    UpdateCheckoutFailurePayload,
    UpdateCheckoutPayload,
    UpdateCheckoutSuccessPayload
} from "./checkout-action-types";
import {createAction} from "redux-actions";
import {of} from "rxjs/observable/of";
import {Checkout, UserError} from "../../@types/shopify";

export const createCheckout = createAction<CreateCheckoutPayload>(CHECKOUT_CREATE_REQUEST);
export const createCheckoutSuccess = createAction<CreateCheckoutSuccessPayload, Checkout>(CHECKOUT_CREATE_SUCCESS, (checkout) => ({checkout}));
export const createCheckoutSuccess$ = (checkout: Checkout) => of(createCheckoutSuccess(checkout));
export const createCheckoutFailure = createAction<CreateCheckoutFailurePayload, UserError[]>(CHECKOUT_CREATE_FAILURE, (errors) => ({errors}));
export const createCheckoutFailure$ = (errors: UserError[]) => of(createCheckoutFailure(errors));

export const fetchCheckout = createAction<FetchCheckoutPayload, string>(CHECKOUT_FETCH_REQUEST, (id) => ({id}));
export const fetchCheckoutSuccess = createAction<FetchCheckoutSuccessPayload, Checkout>(CHECKOUT_FETCH_SUCCESS, (checkout) => ({checkout}));
export const fetchCheckoutSuccess$ = (checkout: Checkout) => of(createCheckoutSuccess(checkout));
export const fetchCheckoutFailure = createAction<FetchCheckoutFailurePayload, UserError[]>(CHECKOUT_FETCH_FAILURE, (errors) => ({errors}));
export const fetchCheckoutFailure$ = (errors: UserError[]) => of(createCheckoutFailure(errors));

export const updateCheckout = createAction<UpdateCheckoutPayload>(CHECKOUT_UPDATE_REQUEST);
export const updateCheckoutSuccess = createAction<UpdateCheckoutSuccessPayload, Checkout>(CHECKOUT_UPDATE_SUCCESS, (checkout) => ({checkout}));
export const updateCheckoutSuccess$ = (checkout: Checkout) => of(updateCheckoutSuccess(checkout));
export const updateCheckoutFailure = createAction<UpdateCheckoutFailurePayload, UserError[]>(CHECKOUT_UPDATE_FAILURE, (errors) => ({errors}));
export const updateCheckoutFailure$ = (errors: UserError[]) => of(updateCheckoutFailure(errors));


