import {Dispatch, Middleware, Store} from "redux";
import {LOCAL_STORAGE_SAVE, LocalStoragePayload} from "./redux-local-storage-actions";
import {Action} from "redux-actions";

export const localStorageMiddleware = <S>(): Middleware => {
    return (store: Store<S>) => (next: Dispatch<S>) => (action: Action<LocalStoragePayload>) => {
        if (action.type === LOCAL_STORAGE_SAVE) {
            const {key, value} = action.payload;
            window.localStorage.setItem(key, JSON.stringify(value));
        } else {
            return next(action);
        }
    };
};



