import {ReduxBehavior} from "./adv-redux-behavior";
import {getCountryList} from "../state/shipping/shipping-selectors";

import {getEmail, hasCheckoutId} from "../state/checkout/checkout-selectors";
import {changeEmail, changeNote} from "../state/checkout/checkout-actions";
import {
    changeAddress,
    changeAddressExtra,
    changeCity,
    changeCountry,
    changeFirstName,
    changeLastName,
    changePhone,
    changeZip
} from "../state/shipping/shipping-actions";
import {createCheckout, updateCheckout} from "../state/checkout/checkout-epic-actions";
import {changeCvv, changeExpiration, changeName, changeNumber} from "../state/payment/payment-actions";

class AdvPaymentForm {
    get is() {
        return "adv-payment-form";
    }

    get behaviors() {
        return [ReduxBehavior];
    }

    get properties() {
        return {
            name: {
                type: String, notify: false, statePath: getEmail
            }
        };
    }

    get listeners() {
        return {
            'ccName.change': '_onNameChange',
            'ccNumber.change': '_onNumberChange',
            'ccExp.change': '_onExpirationChange',
            'ccCvv.change': '_onCvvChange',
        };
    }

    get actions() {
        return {

            changeName(name: string) {
                return changeName(name);
            },

            changeNumber(number: string) {
                return changeNumber(number);
            },

            changeExpiration(expiration: string) {
                return changeExpiration(expiration);
            },

            changeCvv(cvv: string) {
                return changeCvv(cvv);
            }
        };
    }

    _onNameChange(event) {
        const value = event.target.value;
        this.dispatch('changeName', value);
    }

    _onNumberChange(event) {
        const value = event.target.value;
        this.dispatch('changeNumber', value);
    }

    _onExpirationChange(event) {
        const value = event.target.value;
        this.dispatch('changeExpiration', value);
    }

    _onCvvChange(event) {
        const value = event.target.value;
        this.dispatch('changeCvv', value);
    }

    _createCheckout() {
        const state = this.getState();
        if(hasCheckoutId(state)) {
            this.dispatch('updateCheckout');
        } else {
            this.dispatch('createCheckout');
        }
    }
}

Polymer(AdvPaymentForm);




