import {ReduxBehavior} from "./adv-redux-behavior";
import {fetchShop} from "../state/shop/shop-actions";

export class AdvShop {

    get is() {
        return 'adv-shop';
    }

    get behaviors() {
        return [ReduxBehavior];
    }

    get properties() {
        return {
            route: Object,

            subRoute: Object,

            pageData: {
                type: Object,
                observer: '_pageDataChanged'
            },

            page: String,

            _scrollPositionMap: {
                type: Object,
                value() {
                    return {};
                }
            }
        };
    }

    ready() {
        this.dispatch(fetchShop());
    }

    attached() {
        this.async(function () {
            if (!this.route.path || this.route.path === '/') {
                this.set('route.path', '/home');
            }
        });
    }

    /**
     * Preserves the document scroll position, so
     * it can be restored when returning to a page.
     */
    _pageDataChanged(pageData, oldPageData) {
        const map = this._scrollPositionMap;
        if (!!oldPageData && oldPageData.page !== null) {
            map[oldPageData.page] = window.pageYOffset;
        }
        this.page = pageData.page;
        if (map[pageData.page]) {
            Polymer.AppLayout.scroll({top: map[pageData.page], behavior: 'silent'});
        } else if (this.isAttached) {
            Polymer.AppLayout.scroll({top: 0, behavior: 'silent'});
        }
    }
}

Polymer(AdvShop);



