import {StoreFactory} from "./redux/StoreFactory";
import {appEpics} from "./state/app-epics";
import {combineReducers} from "redux";
import {Client} from "./services/ShopifyService";
import {createEpicMiddleware} from "redux-observable";
import {AppState} from "./state/app-state";
import {appReducer} from "./state/app-reducer";
import {localStorageMiddleware} from "./redux/middleware/redux-local-storage";
import {LocalStorageService} from "./services/LocalStorageService";
import {fetchCheckout} from "./state/checkout/checkout-epic-actions";

const client = Client.createInstance(process.env.API_URL, process.env.API_OPTIONS);
const apolloReducer = client.apollo.reducer();
const apolloMiddleware = client.apollo.middleware();

const rootReducer = combineReducers<AppState>({
    ...appReducer,
    apollo: apolloReducer
});

const middleware = [
    createEpicMiddleware(appEpics),
    apolloMiddleware,
    localStorageMiddleware()
];

const store = StoreFactory.createInstance(rootReducer, {}, middleware);

const localStorage = new LocalStorageService();
const checkoutId = localStorage.getCheckoutId();

if(checkoutId !== null) {
    store.dispatch(fetchCheckout(checkoutId));
}


