import {createAction} from "redux-actions";

export const LOCAL_STORAGE_SAVE = 'LOCAL_STORAGE_SAVE';

export type LocalStoragePayload = { key: string, value: any };
export const saveToLocalStorage = createAction<LocalStoragePayload, string, string>(LOCAL_STORAGE_SAVE, (key: string, value: any) => ({
    key,
    value
}));


