import {ActionsObservable, combineEpics} from "redux-observable";
import {
    CHECKOUT_CREATE_REQUEST,
    CHECKOUT_CREATE_SUCCESS,
    CHECKOUT_FETCH_REQUEST, CHECKOUT_UPDATE_REQUEST,
    CreateCheckoutPayload,
    CreateCheckoutSuccessPayload,
    FetchCheckoutPayload, UpdateCheckoutPayload
} from "./checkout-action-types";
import {AppState} from "../app-state";
import {Store} from "redux";
import {Client} from "../../services/ShopifyService";
import * as CheckoutSelectors from "./checkout-selectors";
import * as ShippingAddressSelectors from "../shipping/shipping-selectors";
import {CheckoutCreateInput} from "../../@types/shopify";
import {
    createCheckoutFailure$,
    createCheckoutSuccess,
    fetchCheckoutFailure$,
    fetchCheckoutSuccess, updateCheckoutFailure$, updateCheckoutSuccess
} from "./checkout-epic-actions";
import {Action} from "redux-actions";
import {saveCurrentCheckoutId} from "./checkout-actions";



export const createCheckoutEpic = (action$: ActionsObservable<Action<CreateCheckoutPayload>>, store: Store<AppState>) => {
    return action$.ofType(CHECKOUT_CREATE_REQUEST).switchMap(action => {
        const client = Client.getInstance();
        const state = store.getState();

        const email = CheckoutSelectors.getEmail(state);
        const note = CheckoutSelectors.getNote(state);
        const lineItems = CheckoutSelectors.getLineItems(state);
        const shippingAddress = ShippingAddressSelectors.getShippingAddress(state);

        const input: CheckoutCreateInput = {
            email,
            note,
            lineItems,
            shippingAddress,
            customAttributes: [],
            allowPartialAddresses: true
        };
        return client.createCheckout$(input)
            .map((checkout) => createCheckoutSuccess(checkout))
            .catch(errors => createCheckoutFailure$(errors));
    });
};

export const createCheckoutSuccessEpic = (action$: ActionsObservable<Action<CreateCheckoutSuccessPayload>>, store: Store<AppState>) => {
    return action$.ofType(CHECKOUT_CREATE_SUCCESS).map(action => {
        const checkoutId = action.payload.checkout.id;
        return saveCurrentCheckoutId(checkoutId);
    });
};

export const fetchCheckoutEpic = (action$: ActionsObservable<Action<FetchCheckoutPayload>>, store: Store<AppState>) => {
    return action$.ofType(CHECKOUT_FETCH_REQUEST).switchMap(action => {
        const client = Client.getInstance();
        const id = action.payload.id;
        return client.fetchCheckout$(id)
            .map((checkout) => fetchCheckoutSuccess(checkout))
            .catch(errors => fetchCheckoutFailure$(errors));
    });
};

export const updateCheckoutEpic = (action$: ActionsObservable<Action<UpdateCheckoutPayload>>, store: Store<AppState>) => {
    return action$.ofType(CHECKOUT_UPDATE_REQUEST).switchMap(action => {
        const client = Client.getInstance();
        const state = store.getState();
        const checkoutId = CheckoutSelectors.getCheckoutId(state);
        const email = CheckoutSelectors.getEmail(state);
        const shippingAddress = ShippingAddressSelectors.getShippingAddress(state);

        return client.updateCheckout$(checkoutId, email, shippingAddress)
            .map((checkout) => updateCheckoutSuccess(checkout))
            .catch(errors => updateCheckoutFailure$(errors));
    });
};

export const checkoutEpics = combineEpics(
    createCheckoutEpic,
    createCheckoutSuccessEpic,
    fetchCheckoutEpic,
    updateCheckoutEpic
);


