import {createAction} from "redux-actions";

import {of} from "rxjs/observable/of";
import {Shop} from "../../@types/shopify";
import {
    FETCH_SHOP_FAILURE,
    FETCH_SHOP_REQUEST,
    FETCH_SHOP_SUCCESS,
    FetchShopPayload,
    FetchShopSuccessPayload
} from "./shop-action-types";

export const fetchShop = createAction<FetchShopPayload>(FETCH_SHOP_REQUEST);

export const fetchShopSuccess = createAction<FetchShopSuccessPayload, Shop>(FETCH_SHOP_SUCCESS, (shop: Shop) => ({shop}));
export const fetchShopSuccess$ = (shop: Shop) => of(fetchShopSuccess(shop));

export const fetchShopFailure = createAction(FETCH_SHOP_FAILURE, (error: Error) => ({error}));
export const fetchShopFailure$ = (error: Error) => of(fetchShopFailure(error));




