import {
    AddressChangedPayload,
    AddressExtraChangedPayload,
    CityChangedPayload,
    CountryChangedPayload,
    FirstNameChangedPayload,
    LastNameChangedPayload,
    PhoneChangedPayload,
    SHIPPING_ADDRESS_CHANGED,
    SHIPPING_ADDRESS_EXTRA_CHANGED,
    SHIPPING_CITY_CHANGED,
    SHIPPING_COUNTRY_CHANGED,
    SHIPPING_FIRSTNAME_CHANGED,
    SHIPPING_LASTNAME_CHANGED,
    SHIPPING_PHONE_CHANGED,
    SHIPPING_ZIP_CHANGED,
    ZipChangedPayload
} from "./shipping-action-types";
import {createAction} from "redux-actions";

export const changeFirstName = createAction<FirstNameChangedPayload, string>(SHIPPING_FIRSTNAME_CHANGED, (firstName) => ({firstName}));
export const changeLastName = createAction<LastNameChangedPayload, string>(SHIPPING_LASTNAME_CHANGED, (lastName) => ({lastName}));

export const changeAddress = createAction<AddressChangedPayload, string>(SHIPPING_ADDRESS_CHANGED, (address1) => ({address1}));
export const changeAddressExtra = createAction<AddressExtraChangedPayload, string>(SHIPPING_ADDRESS_EXTRA_CHANGED, (address2) => ({address2}));

export const changeCity = createAction<CityChangedPayload, string>(SHIPPING_CITY_CHANGED, (city) => ({city}));
export const changeZip = createAction<ZipChangedPayload, string>(SHIPPING_ZIP_CHANGED, (zip) => ({zip}));
export const changeCountry = createAction<CountryChangedPayload, string>(SHIPPING_COUNTRY_CHANGED, (country) => ({country}));

export const changePhone = createAction<PhoneChangedPayload, string>(SHIPPING_PHONE_CHANGED, (phone) => ({phone}));





