import ApolloClient from "apollo-client/ApolloClient";
import {ApolloQueryResult, createNetworkInterface} from "apollo-client";
import {
    Checkout,
    CheckoutCreateInput,
    CheckoutCreatePayload,
    Collection, MailingAddressInput,
    Node,
    ProductsQueryRoot,
    Shop
} from "../@types/shopify";
import {fromPromise} from "rxjs/observable/fromPromise";
import {Observable} from "rxjs/Observable";
import {NetworkInterfaceOptions} from "apollo-client/transport/networkInterface";
import Result = ProductsQueryRoot.Result;

const checkoutCreate = require("./mutation/CheckoutCreate.graphql");
const checkoutUpdate = require("./mutation/CheckoutUpdate.graphql");
const checkoutByIdQuery = require("./query/CheckoutById.graphql");

const shopQuery = require("./query/Shop.graphql");
const collectionByIdQuery = require("./query/CollectionById.graphql");

export class Client {

    private client: ApolloClient;
    private static INSTANCE: Client;

    constructor(uri: string, options: NetworkInterfaceOptions = {}) {
        const networkInterface = createNetworkInterface({uri, opts: options});
        this.client = new ApolloClient({networkInterface});
    }

    get apollo() {
        return this.client;
    }

    static createInstance(uri: string, options: NetworkInterfaceOptions = {}) {
        if (!Client.INSTANCE) {
            Client.INSTANCE = new Client(uri, options);
        }
        return Client.getInstance();
    }

    static getInstance() {
        return Client.INSTANCE;
    }

    fetchShop(): Promise<Shop> {
        return this.client.query({query: shopQuery})
            .then((response: ApolloQueryResult<Result>) => response.data.shop)
            .catch(error => console.error(error));
    }

    fetchShop$(): Observable<Shop> {
        return fromPromise(this.fetchShop());
    }

    fetchHey(): Promise<Collection> {
        return this.client.query({query: collectionByIdQuery})
            .then((response: ApolloQueryResult<{ node: Node }>) => response.data.node as Collection)
            .catch(error => console.error(error));
    }

    createCheckout(input: CheckoutCreateInput): Promise<Checkout> {
        const options = {mutation: checkoutCreate, variables: {input}};
        return this.client.mutate(options).then((response: ApolloQueryResult<{ checkoutCreate: CheckoutCreatePayload }>) => {
            const {userErrors, checkout} = response.data.checkoutCreate;
            if (userErrors.length > 0) {
                return Promise.reject(userErrors);
            } else {
                return Promise.resolve(checkout);
            }
        });
    }

    createCheckout$(input: CheckoutCreateInput): Observable<Checkout> {
        return fromPromise(this.createCheckout(input));
    }

    fetchCheckout(id: string): Promise<Checkout> {
        return this.client.query({query: checkoutByIdQuery, variables: {id}})
            .then((response: ApolloQueryResult<{ node: Node }>) => response.data.node as Checkout)
            .catch(error => {
                console.error('ERROR: Failed to fetch checkout', error);
                return Promise.reject(error);
            });
    }

    fetchCheckout$(id: string): Observable<Checkout> {
        return fromPromise(this.fetchCheckout(id));
    }

    updateCheckout(checkoutId: string, email: string, shippingAddress: MailingAddressInput) {
        const options = {mutation: checkoutUpdate, variables: {checkoutId, email, shippingAddress}};
        return this.client.mutate(options).then((response: ApolloQueryResult<{ checkoutCreate: CheckoutCreatePayload }>) => {
            const {userErrors, checkout} = response.data.checkoutCreate;
            if (userErrors.length > 0) {
                return Promise.reject(userErrors);
            } else {
                return Promise.resolve(checkout);
            }
        });
    }

    updateCheckout$(checkoutId: string, email: string, shippingAddress: MailingAddressInput) {
        return fromPromise(this.updateCheckout(checkoutId, email, shippingAddress));
    }
}




