import {FETCH_SHOP_SUCCESS, FetchShopSuccessPayload} from "./shop-action-types";
import {Action} from "redux-actions";
import {Shop} from "./shop-state";

export const shop = (state: Shop = {}, action: Action<FetchShopSuccessPayload>): Shop => {
    if (action.type === FETCH_SHOP_SUCCESS) {
        return action.payload.shop;
    }
    return state;
};



