export const CHECKOUT_ID_STORAGE_KEY = 'advolat-checkout-id';

export class LocalStorageService {

    getCheckoutId(): string | null {
        if (typeof window !== "undefined") {
            const item = window.localStorage.getItem(CHECKOUT_ID_STORAGE_KEY);
            if (item) {
                return JSON.parse(item);
            }
        }
        return null;
    }
}




