export class AdvHtml {

    get is() {
        return 'adv-html';
    }

    get properties() {
        return {
            html: {
                type: String,
                notify: false,
                value: '',
                observer: '_onHtmlChanged'
            }
        };
    }

    ready() {
        this._innerHtml(this.html);
    }

    _innerHtml(html = '') {
        Polymer.dom(this).innerHTML = html || '';
    }

    _onHtmlChanged(newHtml, oldHtml) {
        this._innerHtml(newHtml || '');
    }
}

Polymer(AdvHtml);



