import {compose, Middleware, Reducer, Store} from "redux";
import {AppState} from "../state/app-state";
import {ReduxStore} from "./Store";

/**
 * Singleton instance of an app state shared across the application
 *
 * @type {Store}
 */
export class StoreFactory {

    private static INSTANCE: ReduxStore<AppState> = null;

    private static createStore(rootReducer: Reducer<AppState>, initialState: AppState = {}, middleware: Middleware[]) {
        const composeEnhancers = (<any>window).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
        return new ReduxStore<AppState>(rootReducer, initialState, middleware, composeEnhancers);
    }

    static createInstance(rootReducer: Reducer<AppState>, initialState: AppState = {}, middleware: Middleware[]): ReduxStore<AppState> {
        if (!StoreFactory.INSTANCE) {
            StoreFactory.INSTANCE = StoreFactory.createStore(rootReducer, initialState, middleware);
        }
        return StoreFactory.getInstance();
    }

    static getInstance(): ReduxStore<AppState> {
        return StoreFactory.INSTANCE;
    }

}



