import {createAction} from "redux-actions";
import {
    CREDIT_CARD_EXPIRATION_CHANGED,
    CREDIT_CARD_NAME_CHANGED,
    CREDIT_CARD_NUMBER_CHANGED,
    CREDIT_CARD_VERIFICATION_CHANGED,
    CreditCardExpirationChangedPayload,
    CreditCardNameChangedPayload,
    CreditCardNumberChangedPayload,
    CreditCardVerificationChangedPayload
} from "./payment-action-types";

export const changeName = createAction<CreditCardNameChangedPayload, string>(CREDIT_CARD_NAME_CHANGED, (name) => ({name}));
export const changeNumber = createAction<CreditCardNumberChangedPayload, string>(CREDIT_CARD_NUMBER_CHANGED, (number) => ({number}));
export const changeExpiration = createAction<CreditCardExpirationChangedPayload, string>(CREDIT_CARD_EXPIRATION_CHANGED, (expiration) => ({expiration}));
export const changeCvv = createAction<CreditCardVerificationChangedPayload, string>(CREDIT_CARD_VERIFICATION_CHANGED, (cvv) => ({cvv}));


