import {createAction} from "redux-actions";
import {
    CHECKOUT_EMAIL_CHANGED,
    CHECKOUT_NOTE_CHANGED,
    EmailChangedPayload,
    NoteChangedPayload
} from "./checkout-action-types";
import {saveToLocalStorage} from "../../redux/middleware/redux-local-storage-actions";
import {CHECKOUT_ID_STORAGE_KEY} from "../../services/LocalStorageService";

export const changeEmail = createAction<EmailChangedPayload, string>(CHECKOUT_EMAIL_CHANGED, (email) => ({email}));
export const changeNote = createAction<NoteChangedPayload, string>(CHECKOUT_NOTE_CHANGED, (note) => ({note}));

export const saveCurrentCheckoutId = (checkoutId: string) => saveToLocalStorage(CHECKOUT_ID_STORAGE_KEY, checkoutId);



