import {combineReducers} from "redux";
import {Action} from "redux-actions";
import {
    CHECKOUT_CREATE_SUCCESS,
    CHECKOUT_EMAIL_CHANGED, CHECKOUT_FETCH_SUCCESS,
    CHECKOUT_NOTE_CHANGED,
    CreateCheckoutSuccessPayload,
    EmailChangedPayload, FetchCheckoutSuccessPayload,
    NoteChangedPayload
} from "./checkout-action-types";
import {Checkout} from "./checkout-state";
import {CheckoutLineItem} from "../../@types/shopify";

export const id = (state: string | null = null, action: Action<CreateCheckoutSuccessPayload>): string => {
    if (action.type === CHECKOUT_CREATE_SUCCESS) {
        return action.payload.checkout.id;
    }
    return state;
};

export const email = (state: string | null = null, action: Action<EmailChangedPayload>): string => {
    if (action.type === CHECKOUT_EMAIL_CHANGED) {
        return action.payload.email;
    }
    return state;
};

export const lineItems = (state: CheckoutLineItem[] = [], action: Action<any>): CheckoutLineItem[] => {
    return state;
};

export const note = (state: string | null = null, action: Action<NoteChangedPayload>): string => {
    if (action.type === CHECKOUT_NOTE_CHANGED) {
        return action.payload.note;
    }
    return state;
};

export const checkoutReducer = combineReducers({
    id, email, lineItems, note
});

export const checkout = (state: Checkout = {}, action: Action<FetchCheckoutSuccessPayload>) => {

    if (action.type === CHECKOUT_FETCH_SUCCESS) {
        const checkout = action.payload.checkout;
        return {
            id: checkout.id,
            email: checkout.email,
            lineItems: checkout.lineItems,
            note: checkout.note
        };
    }

    return checkoutReducer(state, action);
};



