import {Action} from "redux-actions";
import {combineReducers} from "redux";
import {MailingAddressInput} from "./shipping-state";
import {
    AddressChangedPayload,
    AddressExtraChangedPayload,
    CityChangedPayload,
    CountryChangedPayload,
    FirstNameChangedPayload,
    LastNameChangedPayload,
    PhoneChangedPayload,
    ProvinceChangedPayload,
    SHIPPING_ADDRESS_CHANGED,
    SHIPPING_ADDRESS_EXTRA_CHANGED,
    SHIPPING_CITY_CHANGED,
    SHIPPING_COUNTRY_CHANGED,
    SHIPPING_FIRSTNAME_CHANGED,
    SHIPPING_LASTNAME_CHANGED,
    SHIPPING_PHONE_CHANGED,
    SHIPPING_PROVINCE_CHANGED,
    SHIPPING_ZIP_CHANGED,
    ZipChangedPayload
} from "./shipping-action-types";
import {CountryType} from "./Country";
import {CHECKOUT_FETCH_SUCCESS, FetchCheckoutSuccessPayload} from "../checkout/checkout-action-types";

export const address1 = (state: string | null = null, action: Action<AddressChangedPayload>): string => {
    if (action.type === SHIPPING_ADDRESS_CHANGED) {
        return action.payload.address1;
    }
    return state;
};

export const address2 = (state: string | null = null, action: Action<AddressExtraChangedPayload>): string => {
    if (action.type === SHIPPING_ADDRESS_EXTRA_CHANGED) {
        return action.payload.address2;
    }
    return state;
};

export const city = (state: string | null = null, action: Action<CityChangedPayload>): string => {
    if (action.type === SHIPPING_CITY_CHANGED) {
        return action.payload.city;
    }
    return state;
};

export const country = (state: string | null = CountryType.DEFAULT, action: Action<CountryChangedPayload>): string => {
    if (action.type === SHIPPING_COUNTRY_CHANGED) {
        return action.payload.country;
    }
    return state;
};

export const province = (state: string | null = null, action: Action<ProvinceChangedPayload>): string => {
    if (action.type === SHIPPING_PROVINCE_CHANGED) {
        return action.payload.province;
    }
    return state;
};

export const firstName = (state: string | null = null, action: Action<FirstNameChangedPayload>): string => {
    if (action.type === SHIPPING_FIRSTNAME_CHANGED) {
        return action.payload.firstName;
    }
    return state;
};

export const lastName = (state: string | null = null, action: Action<LastNameChangedPayload>): string => {
    if (action.type === SHIPPING_LASTNAME_CHANGED) {
        return action.payload.lastName;
    }
    return state;
};

export const zip = (state: string | null = null, action: Action<ZipChangedPayload>): string => {
    if (action.type == SHIPPING_ZIP_CHANGED) {
        return action.payload.zip;
    }
    return state;
};

export const phone = (state: string | null = null, action: Action<PhoneChangedPayload>): string => {
    if (action.type === SHIPPING_PHONE_CHANGED) {
        return action.payload.phone;
    }
    return state;
};

export const shippingAddressReducer = combineReducers<MailingAddressInput>({
    firstName, lastName, address1, address2, city, zip, country, province, phone
});

export const shippingAddress = (state: MailingAddressInput = {}, action: Action<FetchCheckoutSuccessPayload>): MailingAddressInput => {
    if (action.type === CHECKOUT_FETCH_SUCCESS) {
        const address = action.payload.checkout.shippingAddress;
        return {
            firstName: address.firstName,
            lastName: address.lastName,
            address1: address.address1,
            address2: address.address2,
            city: address.city,
            zip: address.zip,
            country: address.country,
            province: address.province,
            phone: address.phone
        };
    }
    return shippingAddressReducer(state, action);
};


