import {ReduxBehavior} from "./adv-redux-behavior";
import {
    getAddress1,
    getAddress2,
    getCity,
    getCountry,
    getCountryList,
    getFirstName,
    getLastName,
    getPhone,
    getZip
} from "../state/shipping/shipping-selectors";

import {getEmail, getNote, hasCheckoutId} from "../state/checkout/checkout-selectors";
import {changeEmail, changeNote} from "../state/checkout/checkout-actions";
import {
    changeAddress,
    changeAddressExtra,
    changeCity,
    changeCountry,
    changeFirstName,
    changeLastName,
    changePhone,
    changeZip
} from "../state/shipping/shipping-actions";
import {createCheckout, updateCheckout} from "../state/checkout/checkout-epic-actions";

class AdvShippingForm {
    get is() {
        return "adv-address-form";
    }

    get behaviors() {
        return [ReduxBehavior];
    }

    get properties() {
        return {
            email: {
                type: String, notify: false, statePath: getEmail
            },
            firstName: {
                type: String, notify: false, statePath: getFirstName
            },
            lastName: {
                type: String, notify: false, statePath: getLastName
            },
            address1: {
                type: String, notify: false, statePath: getAddress1
            },
            address2: {
                type: String, notify: false, statePath: getAddress2
            },
            city: {
                type: String, notify: false, statePath: getCity
            },
            zip: {
                type: String, notify: false, statePath: getZip
            },
            country: {
                type: String, notify: false, statePath: getCountry
            },
            phone: {
                type: String, notify: false, statePath: getPhone
            },
            note: {
                type: String, notify: false, statePath: getNote
            }
        };
    }

    get listeners() {
        return {
            'email.change': '_onEmailChange',
            'firstName.change': '_onFirstNameChange',
            'lastName.change': '_onLastNameChange',

            'address1.change': '_onAddressChange',
            'address2.change': '_onAddressExtraChange',
            'city.change': '_onCityChange',
            'zip.change': '_onZipChange',
            'country.selected-changed': '_onCountryChange',
            'phone.change': '_onPhoneChange',

            // 'note.change': '_onNoteChange',
        };
    }

    get actions() {
        return {

            changeEmail(email) {
                return changeEmail(email);
            },

            changeFirstName(firstName) {
                return changeFirstName(firstName);
            },

            changeLastName(lastName) {
                return changeLastName(lastName);
            },

            changeAddress(address) {
                return changeAddress(address);
            },

            changeAddressExtra(address) {
                return changeAddressExtra(address);
            },

            changeCity(city) {
                return changeCity(city);
            },

            changeZip(zip) {
                return changeZip(zip);
            },

            changeCountry(country) {
                return changeCountry(country);
            },

            changePhone(phone) {
                return changePhone(phone);
            },

            changeNote(phone) {
                return changeNote(phone);
            },

            createCheckout() {
                return createCheckout();
            },

            updateCheckout() {
                return updateCheckout();
            }
        };
    }

    ready() {
        this.countries = getCountryList();
    }

    _onEmailChange(event) {
        const value = event.target.value;
        this.dispatch('changeEmail', value);
    }

    _onFirstNameChange(event) {
        const value = event.target.value;
        this.dispatch('changeFirstName', value);
    }

    _onLastNameChange(event) {
        const value = event.target.value;
        this.dispatch('changeLastName', value);
    }

    _onAddressChange(event) {
        const value = event.target.value;
        this.dispatch('changeAddress', value);
    }

    _onAddressExtraChange(event) {
        const value = event.target.value;
        this.dispatch('changeAddressExtra', value);
    }

    _onCityChange(event) {
        const value = event.target.value;
        this.dispatch('changeCity', value);
    }

    _onZipChange(event) {
        const value = event.target.value;
        this.dispatch('changeZip', value);
    }

    _onCountryChange(event: CustomEvent) {
        const value = event.detail.value;
        this.dispatch('changeCountry', value);
    }

    _onPhoneChange(event) {
        const value = event.target.value;
        this.dispatch('changePhone', value);
    }

    _onNoteChange(event) {
        const value = event.target.value;
        this.dispatch('changeNote', value);
    }

    _createCheckout() {
        const state = this.getState();
        if(hasCheckoutId(state)) {
            this.dispatch('updateCheckout');
        } else {
            this.dispatch('createCheckout');
        }
    }
}

Polymer(AdvShippingForm);




