import {createSelector} from "reselect";
import {AppState} from "../app-state";
import {countryList} from "./Country";

export const getShippingAddress = (state: AppState) => state.shippingAddress;

export const getFirstName = createSelector(getShippingAddress, (address) => address.firstName);
export const getLastName = createSelector(getShippingAddress, (address) => address.lastName);
export const getAddress1 = createSelector(getShippingAddress, (address) => address.address1);
export const getAddress2 = createSelector(getShippingAddress, (address) => address.address2);
export const getCity = createSelector(getShippingAddress, (address) => address.city);
export const getZip = createSelector(getShippingAddress, (address) => address.zip);
export const getCountry = createSelector(getShippingAddress, (address) => address.country);
export const getCountryList = () => countryList;
export const getPhone = createSelector(getShippingAddress, (address) => address.phone);




