import {ActionsObservable, combineEpics} from "redux-observable";
import "rxjs/add/operator/switchMap";
import "rxjs/add/operator/map";
import "rxjs/add/operator/catch";

import {Store} from "redux";
import {Action} from "redux-actions";
import {FETCH_SHOP_REQUEST, FetchShopPayload} from "./shop-action-types";
import {fetchShopFailure$, fetchShopSuccess} from "./shop-actions";
import {AppState} from "../app-state";
import {Client} from "../../services/ShopifyService";

export const fetchShopEpic = (action$: ActionsObservable<Action<FetchShopPayload>>, store: Store<AppState>) => {
    return action$.ofType(FETCH_SHOP_REQUEST)
        .switchMap(action => {
            const client = Client.getInstance();
            return client.fetchShop$().map(response => fetchShopSuccess(response))
                .catch(error => fetchShopFailure$(error));
        });
};

export const shopEpics = combineEpics(fetchShopEpic);



