import {Observer} from "rxjs/Observer";
import {Subscription} from "rxjs/Subscription";
import {Observable, Subscribable} from "rxjs/Observable";
import "rxjs/add/operator/startWith";

import {applyMiddleware, compose, createStore, Middleware, Reducer, Store} from "redux";
import {Action} from "redux-actions";

export class ReduxStore<S> implements Subscribable<S> {

    private store: Store<S>;

    constructor(rootReducer: Reducer<S>, initialState: S, middleware: Middleware[] = [], cmp: Function = compose) {
        const enhancer = cmp(applyMiddleware(...middleware));
        this.store = createStore(rootReducer, initialState, enhancer);
    }

    getStore() {
        return this.store;
    }

    /**
     * Dispatch an action on the store to modify state
     *
     * @param {Action} action
     */
    dispatch(action: Action<any>): Action<any> {
        return this.store.dispatch(action);
    }

    /**
     * Subscribe to state changes
     *
     * @param subscriber
     * @returns {Subscription}
     */
    subscribe(subscriber: (state: S) => void): Subscription {
        return Observable.create((observer: Observer<S>) => {
            return this.store.subscribe(() => observer.next(this.store.getState()));
        }).startWith(this.store.getState()).subscribe(subscriber);
    }

    /**
     * Returns the current state of the store
     */
    getState(): S {
        return this.store.getState();
    }

    replaceReducer(nextReducer: Reducer<S>): void {
        this.store.replaceReducer(nextReducer);
    }
}



