import {AppState} from "../app-state";
import {createSelector} from "reselect";

export const getCheckout = (state: AppState) => state.checkout;
export const getCheckoutId = createSelector(getCheckout, (checkout) => checkout.id);
export const hasCheckoutId = createSelector(getCheckout, (checkout) => checkout.id !== null);
export const getEmail = createSelector(getCheckout, (checkout) => checkout.email);
export const getLineItems = createSelector(getCheckout, (checkout) => checkout.lineItems);
export const getNote = createSelector(getCheckout, (checkout) => checkout.note);


